                .col
                    p Roger Kerin (2018) define la mezcla de producto con la siguiente explicación: “Un producto tiene atributos tangibles que los cinco sentidos del consumidor pueden percibir. Por ejemplo, el Apple Watch de Apple puede tocarse y sus características pueden verse y escucharse. Además, un producto puede tener atributos intangibles, que consisten en su entrega o garantías, que abarcan conceptos abstractos.

                    p Los productos pueden dividirse en duraderos y no duraderos. #[strong Un producto no duradero] es un artículo que se consume en uno o varios usos, como los alimentos y el combustible. #[strong Un producto duradero] es el que por lo general se utiliza muchas veces, como los electrodomésticos, automóviles y teléfonos móviles. 

        p.mt-5 #[strong Los servicios] son actividades o beneficios intangibles que proporciona una organización para satisfacer las necesidades de los consumidores a cambio de dinero u otro tipo de valor. Por último, en marketing, una idea es un pensamiento que lleva a un producto o acción, como un concepto para un nuevo invento o hacer que las personas acudan a votar”.

        p La mayoría de las organizaciones ofrecen una gama de productos y servicios a los consumidores (Kerin,2018), Como las describe a continuación:

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta-avatar-b.mb-5.align-items-center
          img.h90(src='@/assets/curso/t1-i2.svg' alt='AvatarTop')
