            .p-4
              .h3 Un producto único:
              p Es un producto específico que tiene una marca, un tamaño o precio único. 
                span.text-bold  Ejemplo: suavizante para ropa Ultra Downy.
        .tarjeta-avatar-b.mb-5.align-items-center
          img.h90(src='@/assets/curso/t1-i3.svg' alt='AvatarTop')
          .tarjeta.borde-y
            .p-4
              .h3 Una línea de productos
              p Es un grupo de artículos de productos o servicios que están estrechamente relacionados porque satisfacen un tipo de necesidades, se usan juntos, se venden al mismo grupo de consumidores, se distribuyen en los mismos mercados o están dentro de un mismo rango de precios. 
                span.text-bold  Ej. Las líneas de productos de Nike consisten en ropa y zapatos tenis.
        .tarjeta-avatar-b.mb-5.align-items-center
          img.h90(src='@/assets/curso/t1-i4.svg' alt='AvatarTop')
          .tarjeta.borde-y
            .p-4
              .h3 Una mezcla de productos
              p Consiste en todas las líneas de productos que ofrece una organización. Ejemplo:  
                span.text-bold  P&G tiene una gran mezcla de productos que incluye líneas de belleza y cuidado personal 
                | (pasta de dientes Crest y rasuradoras Gillette), así como cuidado para el hogar (suavizante para ropa Downy, detergente Tide y pañales desechables Pampers).

        p.mt-5 El proceso que se desarrolla para que el producto llegue al consumidor final inicia con el diseño del producto o definición de la idea basados en sus necesidades y deseos, después se produce o se materializa esta idea, se ofrece o se pone a disposición, se anuncia o se da a conocer y por último se vende al consumidor final. 

        p Se trata de adecuar los atributos del producto a las necesidades y deseos que quiere satisfacer el consumidor (Martínez, 2014).

        figure.mb-5
        img(src='@/assets/curso/t1-i5.svg', alt='infografia de proceso')

    .sub-ecored.mt-5#tema1_1
        h2 1.1.	Producto

        img.mt-5.img-a(src="@/assets/curso/img_4.png").img-fluid

        p.mt-5 Los productos no sólo son bienes tangibles, como automóviles, computadoras o teléfonos celulares; en una definición amplia, los productos también incluyen servicios, eventos, personas, lugares, organizaciones e ideas o una mezcla de estos elementos. Así, un iPhone de Apple, un Camry de Toyota y un Caffé Mocha de Starbucks son productos; pero también lo son un viaje a Las Vegas, los servicios de inversión y el consejo de su médico familiar.

        p Para Hernández (2017), un producto puede significar un satisfactor de necesidades o ser considerado como un valor agregado para el consumidor. Asimismo, puede catalogarse como un conjunto de atributos y beneficios o visualizarse como un concepto de negocios. Conforme a los diferentes puntos de vista con que se puede ver al producto, iniciemos con el que lo considera un satisfactor de necesidades, por lo que es necesario relacionar el satisfactor que cubre una necesidad, o bien detectar una necesidad y buscar el satisfactor. 

