        p.text-center Fuente: (Hernández,2017)

        p.mt-5 Los productos y servicios según Kotler (2017) se deben considerar en tres 3 niveles, cada nivel genera más valor para el cliente.
        .row.justify-content-center
            .col-lg-6
                img.mt-5.img-a(src="@/assets/curso/img_5.png").img-fluid

        .row.justify-content-center.mt-5.my-5
            .col-10.col-md-9
                ul.list-unstyled
                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong El nivel básico] es el valor fundamental para el cliente y responde a la pregunta: ¿qué está adquiriendo realmente el comprador? Al diseñar productos, los especialistas en marketing primero deben definir lo esencial, los beneficios o servicios que buscan los consumidores para resolver problemas. La gente que compra un iPad de Apple está adquiriendo algo más que una tableta: está comprando entretenimiento, autoexpresión, productividad y conectividad con sus amigos y familiares —es decir, una ventana móvil y personal hacia el mundo. (Kotler,2017)

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | #[strong En el segundo nivel], los encargados de la planeación de los productos deben convertir el beneficio principal en un producto real. Para ello, tendrán que desarrollar las características, el diseño, un nivel de calidad, un nombre de marca y un empaque de los productos y servicios. Por ejemplo, el iPad es un producto real. Su nombre, sus partes, su estilo, su sistema operativo, sus características, su empaque y otros atributos se combinaron cuidadosamente para brindar el valor principal para el cliente de mantenerse conectado. (Kotler,2017)

                    li.justify-content-center.mt-5
                        i.fa.fa-angle-right.fa-lg.mr-3.text-color-orange
                        | Por último, #[strong en el tercer nivel], los encargados de la planeación de los productos deben crear un producto aumentado en torno al beneficio principal y al producto real para ofrecer servicios y beneficios adicionales al cliente. El iPad es más que un dispositivo digital; brinda a los consumidores una solución completa de conectividad. Por lo tanto, cuando los consumidores compran un iPad, Apple y sus distribuidores también podrían ofrecer a los compradores una garantía de algunas de sus partes y mano de obra, servicios rápidos de reparación cuando se necesiten y un sitio web para ingresar en caso de problemas o de dudas (Kotler,2017).

        .row.mt-5
            .col-1.col-md-1.flecha
                img(src="@/assets/curso/img_6.png").img-icon
            .col-9.col-md-9
                p #[strong Clasificación de los productos.]

        p Según Kotler (2017) Los productos se clasifican en dos productos de consumo y productos industriales de acuerdo al tipo de consumidor que los utiliza. 
        .tarjeta.color-primario.p-3.mb-5
            .row.justify-content-around.align-items-center
                .col-3.col-sm-2.col-lg-1
                    img(src="@/assets/curso/t1-i7.svg")
